.btn, .btn:link, .btn:visited {
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 10rem;
  padding: .4rem 4rem;
  font-size: 1.6rem;
  font-weight: 400;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.btn:after {
  content: "";
  z-index: -1;
  background-image: linear-gradient(#ff7730, #ffb900);
  border-radius: 10rem;
  width: 100%;
  height: 100%;
  transition: all .4s;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 1rem 2rem #0003;
}

.btn:hover:after {
  opacity: 0;
  transform: scaleX(1.4)scaleY(1.6);
}

.btn:active, .btn:focus {
  transform: translateY(-1px);
  box-shadow: 0 .5rem 1rem #0003;
}

.btn--orange {
  color: #333;
  background-image: linear-gradient(#ff7730, #ffb900);
}

.btn--animated {
  animation: .5s ease-out .75s backwards moveInBottom;
}

.logo {
  width: 8rem;
  margin: 0 auto;
  display: block;
}

.link:link, .link:visited {
  color: #0000;
  background: linear-gradient(#28b485, #7ed56f);
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 1.4rem;
  font-weight: 500;
  text-decoration: none;
  transition: all .5s;
  display: inline-block;
}

.link:hover, .link:active {
  color: #7ed56f;
  border-radius: 5px;
  box-shadow: 0 1rem 2rem #55c57a80;
}

.form {
  background-color: #eeec;
